<template>
  <div class="register">
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <van-cell-group class="cell">
      <van-field v-model="username" placeholder="" label="" />
      <van-field v-model="mobile" placeholder="" label="" />
      <van-field v-model="pwd" placeholder="" label="" />
      <van-button round type="danger" size="large" @click="register"></van-button>
    </van-cell-group>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      //   registerInfo: {},
      username: "",
      mobile: "",
      pwd: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    register() {
      const { username, mobile, pwd } = this;
      console.log(username, mobile, pwd )
      this.$axios
        .post("/user/register", { username, mobile, pwd })
        .then(res => {
          var data = res.data;
          if (data.code == 1) {
            //    this.$toast(data.msg)
            this.$router.push({ name: "login" });
          }
          Toast(data.msg);
        });
    }
  }
};
</script>
