<template>
  <div id="login">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <van-cell-group class="cell">
      <van-field v-model="username" placeholder="" label="" />
      <van-field v-model="pwd" placeholder="" label="" />
      <van-button round type="danger" size="large" @click="login"></van-button>
    </van-cell-group>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      // loginInfo: {},
      username: "",
      pwd: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "register" });
    },
    login() {
      const { username, pwd } = this;
      this.$axios
        .post("/user/login", { username, pwd })
        .then(res => {
          const { msg, code, token } = res.data;

          if (code == 1) {
            // token
            localStorage.setItem("token", token);

            // ,store
            this.$store.commit("token", token);

            Toast(msg);
            this.$router.push({ name: "user" });
          } else {
            Toast(msg);
          }
          // this.$router.push({ name: "home" });
          // Toast(res.data.msg);
        })
        .catch(err => {
          return console.log(err);
        });
    }
  }
};
</script>

<style scoped>
#login {
  background-color: #000;
}
</style>
