<template>
  <div class="list-container">
    <Head title="" :show="true"></Head>
    <!-- 
            1.mt-loadmore
            2.loadToploadBottomallLoadedref
            3.auto-fillfalse
    -->
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore"
      :auto-fill="false"
    >
      <!-- v-for -->
      <ul class="list">
        <li v-for="(item,index) in list" :key="index" @click="toDetail(item._id)">
          <div><img :src="item.imgs" /></div>
          <p>{{item.title}}</p>
          <p> {{item.price}}</p>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
import Head from "../components/Head.vue";

export default {
  data() {
    return {
      // 
      allLoaded: false,

      // 
      list: [],
      // 
      page: 1
    };
  },
  components: {
    Head
  },
  methods: {
    // ,
    loadTop() {
      // 
      this.$axios
        .post("/list/goodslist", {
          pageNum: 1
        })
        .then(res => {
          const { list } = res.data;

          // 
          this.list = list;
          // 1
          this.page = 1;

          // loading
          this.$refs.loadmore.onTopLoaded();
          // 
          this.allLoaded = false;
        });
    },
    // ,
    loadBottom() {
      // +1
      const newPage = this.page + 1;

      // 
      this.$axios
        .post("/list/goodslist", {
          pageNum: newPage
        })
        .then(res => {
          const { list } = res.data;

          // 
          if (list.length) {
            // 
            this.list = this.list.concat(list);

            // 
            this.page = newPage;
          } else {
            // 
            this.allLoaded = true;
          }

          // 
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    // 
    toDetail(id) {
      this.$router.push({ name: "goodsDetail", params: { goodId: id } });
    }
  },
  // ,,
  mounted() {
    this.loadTop();
  }
};
</script>

<style lang="scss" scoped>
.list-container {
  height: 100vh;
  overflow: scroll;
  // padding-bottom: 1rem;
  // box-sizing: border-box;
}

.list {
  li {
    float: left;
    width: 49%;
    border: 1px solid #eee;
    font-size: 0.3rem;
    // text-align: center;

    div{
      text-align: center;
    }

    p{
      box-sizing:border-box;
      padding-left: 0.1rem;
    }

    img {
      width: 3.5rem;
      height: 3.5rem;
      object-fit: cover;
    }
  }
}
</style>