import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios'

/*
	,statemutation
	,action
*/


export default new Vuex.Store({
  state: {
    token: '',
    username: null
  },
  mutations: {
    token(state, data) {
      state.token = data
    },
    username(state, data) {
      state.username = data
    }
  },
  actions: {
    token({ commit }, data) {
      // actionmutation
      commit('token', data)
    },
    myAction({ state }, data) {
      // tokenid
      return axios.post('/user/cart/add', {
        token: state.token,
        goodsId: data.goodsId,
        goodsNum: data.goodsNum,
        goodsName: data.goodsName,
        goodsPrice: data.goodsPrice,
        goodsImg: data.goodsImg,
        goodsColor: data.goodsColor,
        goodsSize: data.goodsSize
      })
    }
  }
})
